  text-transform: uppercase; }

.navigation__button-burger {
  width: 30px;
  height: 6px;
  border-radius: 3px;
  background-color: #e22028;
  position: relative;
  transition: background-color 0.5s; }
  .navigation__button-burger::before {
    content: '';
    width: 30px;
    height: 6px;
    border-radius: 3px;
    background-color: #e22028;
    position: absolute;
    top: -12px;
    left: 0px;
    transform-origin: left top;
    transition: transform 0.5s; }
  .navigation__button-burger::after {
    content: '';
    width: 30px;
    height: 6px;
    border-radius: 3px;
    background-color: #e22028;
    position: absolute;
    bottom: -12px;
    left: 0;
    transform-origin: left bottom;
    transition: transform 0.5s; }

.navigation__button {
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 20px; }
  .navigation__button:hover {
    opacity: 0.6; }
  .navigation__button:active {
    opacity: 0.8; }
  .navigation__button--clicked .navigation__button-text {
    color: transparent; }
  .navigation__button--clicked .navigation__button-burger {
    background-color: transparent; }
    .navigation__button--clicked .navigation__button-burger::before {
      width: 38px;
      transform: rotate(42deg); }
    .navigation__button--clicked .navigation__button-burger::after {
      width: 38px;
      transform: rotate(-42deg); }

